.SearchContainer{
    max-width: 240px;
    height: 100%;
    width: 100%;
    -webkit-box-shadow: 0px 4px 10px #000;
            box-shadow: 0px 4px 10px #000;
    position: fixed;
    right: 0;
    z-index: 100;
   /* // background: #fff; */
   top: 65px;
   background-image: -webkit-gradient(linear, left bottom, right top, from(#051937), color-stop(#57b6ef), color-stop(#008793), color-stop(#19b274), to(#b9d972));
   background-image: -webkit-linear-gradient(left bottom, #051937, #57b6ef, #008793, #19b274, #b9d972);
   background-image: -o-linear-gradient(left bottom, #051937, #57b6ef, #008793, #19b274, #b9d972);
   background-image: linear-gradient(to right top, #051937, #57b6ef, #008793, #19b274, #b9d972);}
.SearchContainer h3{
    -webkit-box-shadow: 0 0 6px inset #ccc;
            box-shadow: 0 0 6px inset #ccc;
    padding: 15px 10px;
    margin-top: 0;
}
.SpacingTopLarge{
    margin-top: 30px;
}
.SpacingSmallLeftSpacing{
    margin-left: 10px;
}



